.button {
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 10px 30px;
  font-size: 24px;
  font-weight: bold;
  box-shadow: 5px 5px #000c, 0 0 10px #000c;
}

.button:active {
  box-shadow: none;
  outline: none;
  position: relative;
  top: 10px;
  left: 2px;
}

